div.qrCode {
    position: fixed;
    left: 2rem;
    bottom: 2rem;
    width: 18rem;
    height: 20rem;
    z-index: 99;
    cursor: pointer;
    border: 2px solid #ffffff;
    -webkit-border-radius: 1rem;
    -moz-border-radius: 1rem;
    -ms-border-radius: 1rem;
    -o-border-radius: 1rem;
    border-radius: 1rem;
    background: #ffffff;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}
div.qrCode.qrCode-hide {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: transparent;
    border: 0;
}

div.qrCode .qrCode-img {
    width: 80%;
    height: 80%;
    object-fit: cover;
}

div.qrCode .qrCode-img.qrCodeImg-active {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(0, 0, 0, 0.4);
}

div.qrCode .qrCode-img img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

div.qrCode .qrCode-img.qrCodeImg-active img {
    width: 30%;
    height: auto;
    object-fit: cover;
}

div.qrCode p {
    font-size: 1.6rem;
    text-align: center;
    font-weight: 600;
}