    background-position: 0 10rem !important;
    background-repeat: no-repeat !important;
    background-size: cover !important;
    background-attachment: fixed !important;
    padding: 10rem 0 0 !important;
    position: relative;
}

.overlay {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    background: rgba(0, 0,0, 0.6);
    z-index: 1;
}

.section-container {
    padding: 5rem 0;
