    letter-spacing: 0.5rem;
    width: 45%;
    margin-bottom: 3rem;
    text-align: right;
    margin-right: 3rem;
    padding-right: 3rem;
    border-right: 1px solid #eee;
}

.hero-section p {
    font-size: 2.2rem;
