}

.feature-content-1,
.feature-content-2 {
    width: 40%;
    text-align: center;
    padding: 4rem 4rem;
    margin: 0 5rem;
    transition: all 0.5s ease;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 1rem;
    -webkit-border-radius: 1rem;
    -moz-border-radius: 1rem;
    -ms-border-radius: 1rem;
    -o-border-radius: 1rem;
    background: #ffffff;
    box-shadow: 0px -1.6rem 6.01rem 0px rgba(0, 0, 0, 0.16);
    -webkit-backdrop-filter: blur(15.550000190734863px);
    backdrop-filter: blur(15.550000190734863px);
}

.feature-content-1:hover,
.feature-content-2:hover {
    cursor: text;
    box-shadow: 0px 5px 15px -2px black;
    transform: translateY(-1rem);
    -webkit-transform: translateY(-1rem);
    -moz-transform: translateY(-1rem);
    -ms-transform: translateY(-1rem);
    -o-transform: translateY(-1rem);
}

.feature-content-1 img,
.feature-content-2 img {
    width: 100%;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
    margin-bottom: 3rem;
}

