@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');
.main-header {
    width: 100%;
    min-height: 10rem;
    max-height: 10rem;
    background: transparent;
    padding: 2rem 10rem;
    position: fixed !important;
    top: 0;
    right: 0;
    left: 0;
    z-index: 1030;
    -webkit-transition: all 0.4s;
    -moz-transition: all 0.4s;
    -ms-transition: all 0.4s;
    -o-transition: all 0.4s;
    transition: all 0.4s;
    background: #272B28;
    font-family: 'Quicksand',
    sans-serif;
}

.main-header.sticky-header {
    background-color: #f9dada;
    box-shadow: 0 0.125rem 0.125rem -0.125rem rgba(31, 27, 45, .1), 0 0.25rem 0.75rem rgba(31, 27, 45, .1);
    border-bottom: 1px solid #cccccc;
}

.header-container {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    max-width: 144rem;
    margin: 0 auto;
}

.hamburger-btn {
    display: none;
    align-items: center;
    justify-content: center;
}

.hamburger-btn svg {
    width: 2.5rem;
    height: 2.5rem;
    cursor: pointer;
    -webkit-transition: 0.4s;
    -moz-transition: 0.4s;
    -ms-transition: 0.4s;
    -o-transition: 0.4s;
    transition: 0.4s;
    -webkit-animation: fadeIn 0.4s forwards;
    animation: fadeIn 0.4s forwards;
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

#hamburgerMenuClose {
    width: 2.8rem;
    height: 2.8rem;
    display: none;
}

.hamburger-btn svg:hover,
.hamburger-btn svg:focus,
.hamburger-btn svg:active {
    -webkit-transform: scale(1.1);
    -moz-transform: scale(1.1);
    -ms-transform: scale(1.1);
    -o-transform: scale(1.1);
    transform: scale(1.1);
}

.header-logo {
    width: 10rem;
}

.header-logo .logo-link {
    text-decoration: none;
    width: 100%;
    display: inline-block;
    cursor: pointer;
}

.header-logo img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.header-navigation {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
}

.navigation-menu {
    list-style-type: none;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin-right: 2rem;
}

.navigation-menu .menu-link {
    font-size: 1.6rem;
    font-weight: bold;
    color: #ffffff;
    padding: 1.2rem 1.6rem;
    font-style: normal;
    line-height: normal;
    letter-spacing: -0.02rem;
    text-decoration: none;
    margin: 0 0.65rem;
    cursor: pointer;
    -webkit-transition: 0.4s;
    -moz-transition: 0.4s;
    -ms-transition: 0.4s;
    -o-transition: 0.4s;
    transition: 0.4s;
    position: relative;
}

.sticky-header .navigation-menu .menu-link {
    color: #2F3766;
}

.navigation-menu .menu-link:hover,
.navigation-menu .menu-link:focus,
.navigation-menu .menu-link:active {
    color: #bdbdbd;
}

.sticky-header .navigation-menu .menu-link:hover,
.sticky-header .navigation-menu .menu-link:focus,
.sticky-header .navigation-menu .menu-link:active {
    color: #2F3766;
}

.navigation-menu .menu-link::after {
    content: "";
    width: 0%;
    height: 2px;
    position: absolute;
    left: 0;
    bottom: 0;
    background: #bdbdbd;
    -webkit-transition: 0.4s;
    -moz-transition: 0.4s;
    -ms-transition: 0.4s;
    -o-transition: 0.4s;
    transition: 0.4s;
}

.sticky-header .navigation-menu .menu-link::after {
    background: #2F3766;
}

.navigation-menu .menu-link:hover::after,
.navigation-menu .menu-link:focus::after,
.navigation-menu .menu-link:active::after {
    width: 100%;
}

.header-btn {
    color: #000000;
    font-size: 1.6rem;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    letter-spacing: -0.02rem;
    text-decoration: none;
    padding: 1.5rem 2.5rem;
    background: #FAEAEA;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
    border-radius: 10px;
    cursor: pointer;
    -webkit-transition: 0.4s;
    -moz-transition: 0.4s;
    -ms-transition: 0.4s;
    -o-transition: 0.4s;
    transition: 0.4s;
    border: 0;
    outline: 0;
}

.sticky-header .header-btn {
    background: #2F3766;
    color: #ffffff;
}

.header-btn:hover,
.header-btn:focus,
.header-btn:active {
   background: #dbb7b7;
   box-shadow: 0 0.25rem 0.25rem 0.125rem rgba(253, 86, 49, .1),
   0 0.375rem 0.75rem -0.125rem rgba(56, 37, 32, 0.4) !important;
}

.sticky-header .header-btn:hover,
.sticky-header .header-btn:focus,
.sticky-header .header-btn:active {
   background: #191f3d;
   box-shadow: 0 0.25rem 0.25rem 0.125rem rgba(253, 86, 49, .1),
   0 0.375rem 0.75rem -0.125rem rgba(56, 37, 32, 0.4) !important;
}

#headerLoginBtn {
    margin-right: 1.5rem;
}