.main-section.placeOrder-hero-section {
    width: 100% !important;
    height: calc(100vh - 10rem) !important;
    margin-top: 10rem !important;
    background: url(../../resources/images/Suff-ai-Title-Image-large-reso.png) !important;
    background-position: 0 10rem !important;
    background-repeat: no-repeat !important;
    background-size: cover !important;
    background-attachment: fixed !important;
    padding: 10rem 0 0 !important;
    position: relative;
}

.overlay {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    background: rgba(0, 0, 0, 0.6);
    z-index: 1;
}

.section-container {
    padding: 5rem 0;
    max-width: 144rem;
    margin: 0 auto;
    position: relative;
    z-index: 2;
}

.main-section.placeOrder-hero-section .section-container {
    padding-top: 20rem;
    display: flex;
    flex-direction: row;
    /* align-items: center;
    justify-content: center; */
}

.placeOrder-hero-section-heading {
    font-size: 8rem;
    color: #fff;
    line-height: normal;
    letter-spacing: 0.5rem;
    width: 100%;
    margin-bottom: 3rem;
    text-align: center;
    margin-right: 3rem;
    padding-right: 3rem;
    text-transform: capitalize;
}

.howItWorks-content {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.howItWorks-content-left,
.howItWorks-content-right {
    width: 47%;
}

.howItWorks-content-left-feature-image {
    width: 100%;
    overflow: hidden;
    border-radius: 2.4rem;
    -webkit-border-radius: 2.4rem;
    -moz-border-radius: 2.4rem;
    -ms-border-radius: 2.4rem;
    -o-border-radius: 2.4rem;
}

.howItWorks-content-left-feature-image img {
    width: 100%;
    object-fit: cover;
    border-radius: 2.4rem;
    -webkit-border-radius: 2.4rem;
    -moz-border-radius: 2.4rem;
    -ms-border-radius: 2.4rem;
    -o-border-radius: 2.4rem;
    transform: scale(1.01);
    -webkit-transform: scale(1.1);
    -moz-transform: scale(1.01);
    -ms-transform: scale(1.01);
    -o-transform: scale(1.01);
}


/* .service-section-heading,
.feature-section-heading,
.whyUs-section-heading, */
.howItWorks-section-heading {
    text-align: left;
    font-size: 4.5rem;
    color: #000000;
    line-height: normal;
    letter-spacing: 0.2rem;
    margin-bottom: 3rem;
}

.howItWorks-section-text {
    width: 100%;
    list-style: none;
    padding: 0;
}

.howItWorks-section-text li:before {
    content: '✓';
    color: #41326F;
    margin-right: 0.8rem;
    font-weight: 900;
}

.howItWorks-section-text li {
    font-size: 2rem;
    font-weight: 500;
    line-height: 3.5rem;
}