@font-face {
  font-family: 'Magiesta';
  src: url('./resources/fonts/magiesta.ttf') format('truetype');
}

@font-face {
  font-family: 'GrottaTrial';
  font-weight: 300;
  src: url('./resources/fonts/Grotta-Trial/Grotta-Trial-Light.ttf') format('truetype');
}

@font-face {
  font-family: 'GrottaTrial';
  font-weight: 400;
  src: url('./resources/fonts/Grotta-Trial/Grotta-Trial-Regular.ttf') format('truetype');
}

@font-face {
  font-family: 'GrottaTrial';
  font-weight: 500;
  src: url('./resources/fonts/Grotta-Trial/Grotta-Trial-Medium.ttf') format('truetype');
}

@font-face {
  font-family: 'GrottaTrial';
  font-weight: 600;
  src: url('./resources/fonts/Grotta-Trial/Grotta-Trial-Semibold.ttf') format('truetype');
}

@font-face {
  font-family: 'GrottaTrial';
  font-weight: 700;
  src: url('./resources/fonts/Grotta-Trial/Grotta-Trial-Bold.ttf') format('truetype');
}

@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');

*,
::after,
::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 10px;
  scroll-behavior: smooth;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -ms-font-smoothing: antialiased;
}

body {
  background: #FAEAEA;
  background: transparent;
  overflow: hidden;
  overflow-y: auto;
  font-family: 'GrottaTrial',
  sans-serif;
  min-height: 100vh;
  
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Magiesta',
  sans-serif;
}