.main-footer {
    width: 100%;
    background: rgb(248, 248, 248);
    padding: 5rem 10rem 2rem;
}

.footer-container {
    width: 100%;
    max-width: 144rem;
    margin: 0 auto;
    padding-bottom: 5rem;
    border-bottom: 1px solid rgb(207, 207, 207);
}

.footer-container .footer-logo {
    width: 15rem;
    margin-bottom: 2.2rem;
}

.footer-container .footer-logo img {
    width: 100%;
    object-fit: cover;
}

.footer-content {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-between;

}

.footer-content-1,
.footer-content-2,
.footer-content-3,
.footer-content-4,
.footer-content-5 {
    width: 19.5%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
}

.footer-content-1 h3,
.footer-content-2 h3,
.footer-content-3 h3,
.footer-content-4 h3,
.footer-content-5 h3 {
    color: #1F1F1F;
    font-size: 2.2rem;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    margin-top: 1.5rem;
    margin-bottom: 1.7rem;
}

.footer-content-1 a,
.footer-content-2 a,
.footer-content-3 a,
.footer-content-4 a,
.footer-content-5 a {
    color: #1F1F1F;
    font-size: 1.8rem;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    margin-bottom: 0.7rem;
    text-decoration: none;
}

.footer-content-1 a:hover,
.footer-content-2 a:hover,
.footer-content-3 a:hover,
.footer-content-4 a:hover,
.footer-content-5 a:hover,
.footer-content-1 a:focus,
.footer-content-2 a:focus,
.footer-content-3 a:focus,
.footer-content-4 a:focus,
.footer-content-5 a:focus,
.footer-content-1 a:active,
.footer-content-2 a:active,
.footer-content-3 a:active,
.footer-content-4 a:active,
.footer-content-5 a:active {
    text-decoration: underline;
}

.social-icons {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.social-icons a {
    width: 3rem;
    height: 3rem;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    border-radius: 50%;
    background: #1c1c1c;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 1rem;
    transition: all 0.3s;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    -ms-transition: all 0.3s;
    -o-transition: all 0.3s;
}

.social-icons a:hover {
    transform: rotate(360deg);
    -webkit-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    -o-transform: rotate(360deg);
}

.social-icons svg {
    width: 2.3rem;
    fill: #ffffff;
}

.footer-copyright {
    width: 100%;
    padding: 3rem 0 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

.footer-copyright p {
    color: #050505;
    font-size: 1.6rem;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}